<template>
  <div>{{ result }}</div>
</template>

<script>
import axios from "axios";

export default {
  name: "HelloWorld",
  data() {
    return {
      result: ""
    };
  },
  created() {
    axios({
      url: "http://localhost:8090/user/queryByName.do",
      method: "POST",
      data: {
        userName: "Kai"
      }
    }).then(res => {
      // console.log(res);
      this.result = res;
    });
  }
};
</script>

<style scope></style>



