<template>
  <div id="app">
    {{ result }}
    <h2>-------HelloWorld------</h2>
    <hello-world />
  </div>
</template>

<script>
import axios from "axios";
import HelloWorld from "./components/HelloWorld";

export default {
  name: "App",
  data() {
    return {
      result: ""
    };
  },
  components: {
    HelloWorld
  },
  created() {
    axios({
      url: "http://localhost:8090/user/queryByName.do",
      params: {
        userName: "Kai"
      }
    }).then(res => {
      // console.log(res);
      this.result = res;
    });
  }
};
</script>

<style></style>



